<template>
  <div>
    <el-row :gutter="20">
      <el-col class="mb20" :xl="6" :lg="6" :md="10" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>栏目编辑</span>
          </div>
          <div>
            <el-form
              :model="editParams"
              label-position="right"
              ref="categoryForm"
              label-width="70px"
              class="vertical-form"
            >
              <el-tabs value="basic">
                <el-tab-pane label="基础信息" name="basic">
                  <el-form-item label="名称">
                    <el-input v-model="editParams.name" prop="name" placeholder="名称"></el-input>
                  </el-form-item>
                  <el-form-item label="父栏目">
                    <tree-select
                      v-if="treeData.length>0"
                      :props="config"
                      :data="treeData"
                      v-model="parentCategory"
                      @node-click="nodeClick"
                    />
                  </el-form-item>
                  <el-form-item label="路径">
                    <el-input v-model="editParams.path" placeholder="访问路径"></el-input>
                  </el-form-item>
                  <el-form-item label="类型">
                    <el-select v-model="editParams.type" placeholder="请选择">
                      <el-option label="导航" :value="1"></el-option>
                      <el-option label="分类" :value="2"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="列表页">
                    <el-select v-model="editParams.listPage" placeholder="请选择">
                      <el-option
                        v-for="template in templates"
                        :key="template.name"
                        :label="template.name"
                        :value="template.name"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="详情页">
                    <el-select v-model="editParams.detailPage" placeholder="请选择">
                      <el-option
                        v-for="template in templates"
                        :key="template.name"
                        :label="template.name"
                        :value="template.name"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="顺序">
                    <el-input v-model="editParams.sort" prop="sort" placeholder="顺序"></el-input>
                  </el-form-item>
                  <el-form-item label="状态">
                    <el-radio-group v-model="editParams.status">
                      <el-radio :label="1">显示</el-radio>
                      <el-radio :label="0">隐藏</el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item label="子栏目">
                    <el-radio-group v-model="editParams.showChildren">
                      <el-radio :label="1">显示</el-radio>
                      <el-radio :label="0">隐藏</el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="扩展字段" name="extField">
                  <el-form-item label="扩展">
                    <el-button type="primary" size="small" @click="showExtForm=true">添加字段</el-button>
                  </el-form-item>
                  <el-form-item v-for="ext in extFields" :key="ext.id" :label="ext.name">
                    <el-input
                      v-if="ext.type=='input'"
                      v-model="categoryExtFields[ext.field]"
                      :placeholder="ext.name"
                      style="width:88%"
                    ></el-input>
                    <el-input
                      v-else-if="ext.type=='textarea'"
                      type="textarea"
                      v-model="categoryExtFields[ext.field]"
                      :placeholder="ext.name"
                      style="width:88%"
                    ></el-input>
                    <el-select
                      v-else-if="ext.type=='select'"
                      v-model="categoryExtFields[ext.field]"
                      :placeholder="ext.name"
                      style="width:88%"
                    >
                      <el-option
                        v-for="val in ext.value.split('，')"
                        :key="val"
                        :label="val"
                        :value="val"
                      ></el-option>
                    </el-select>
                    <el-date-picker
                      v-else-if="ext.type=='date'"
                      v-model="categoryExtFields[ext.field]"
                      type="date"
                      value-format="yyyy-MM-dd"
                      :placeholder="ext.name"
                      style="width:88%"
                    ></el-date-picker>
                    <el-radio-group
                      v-else-if="ext.type=='radio'"
                      v-model="categoryExtFields[ext.field]"
                      style="width:88%"
                    >
                      <el-radio v-for="val in ext.value.split('，')" :key="val" :label="val">{{val}}</el-radio>
                    </el-radio-group>
                    <el-checkbox-group
                      v-else-if="ext.type=='checkbox'"
                      v-model="categoryExtFields[ext.field]"
                      style="width:88%"
                    >
                      <el-checkbox v-for="val in ext.value.split('，')" :key="val" :label="val"></el-checkbox>
                    </el-checkbox-group>
                    <el-upload
                      v-else-if="ext.type=='image'"
                      action="/api/admin/upload/image"
                      :on-remove="handleExtImgRemove"
                      :on-success="onExtImgSuccess"
                      :data="{ext_field:ext.field}"
                      :file-list="categoryExtFields[ext.field]"
                      multiple
                      list-type="picture"
                      style="width:88%;float: left;"
                    >
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                    <el-upload
                      v-else-if="ext.type=='file'"
                      action="/api/admin/upload/file"
                      :on-remove="handleExtFileRemove"
                      :on-success="onExtFileSuccess"
                      :data="{ext_field:ext.field}"
                      :file-list="categoryExtFields[ext.field]"
                      multiple
                      style="width:88%;float: left;"
                    >
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div slot="tip" class="el-upload__tip">不超过500kb</div>
                    </el-upload>
                    <el-button
                      type="text"
                      style="font-size:20px;margin-left:5px;padding: 0;"
                      @click="delExt(ext)"
                    >
                      <i class="el-icon-close"></i>
                    </el-button>
                  </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="高级设置" name="advance">
                  <el-form-item label="副标题">
                    <el-input v-model="editParams.alias" prop="alias" placeholder="副标题"></el-input>
                  </el-form-item>
                  <el-form-item label="链接">
                    <el-input v-model="editParams.outLink" prop="outLink" placeholder="外部链接"></el-input>
                  </el-form-item>
                  <el-form-item label="关键字">
                    <el-input v-model="editParams.keywords" prop="keywords" placeholder="SEO关键字"></el-input>
                  </el-form-item>
                  <el-form-item label="描述">
                    <el-input v-model="editParams.description" type="textarea" placeholder="SEO描述"></el-input>
                  </el-form-item>
                  <el-form-item label="图标">
                    <el-upload
                      action="/api/admin/upload/image"
                      :on-remove="handleRemove"
                      :on-success="onSuccess"
                      :file-list="fileList"
                      :limit="1"
                      list-type="picture"
                    >
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                  </el-form-item>
                  <el-form-item label="大图">
                    <el-upload
                      action="/api/admin/upload/image"
                      :on-remove="handleImgRemove"
                      :on-success="onImgSuccess"
                      :file-list="imgList"
                      :limit="1"
                      list-type="picture"
                    >
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                  </el-form-item>
                  <el-form-item label="多图">
                    <el-upload
                      action="/api/admin/upload/image"
                      :on-remove="handleMultRemove"
                      :on-success="onMultSuccess"
                      :file-list="multFileList"
                      multiple
                      list-type="picture"
                    >
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                  </el-form-item>
                </el-tab-pane>
              </el-tabs>

              <el-form-item>
                <el-button type="primary" size="medium" @click="save()">保存</el-button>
                <el-button size="medium" @click="reset()">重置</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-card>
      </el-col>
      <el-col class="mb20" :xl="18" :lg="18" :md="14" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>栏目分类</span>
          </div>
          <div>
            <vxe-table
              tree-config
              highlight-hover-row
              :border="false"
              ref="categoryTree"
              :edit-config="{trigger: 'click', mode: 'cell'}"
              :data="categories"
              row-id="id"
            >
              <vxe-table-column field="name" title="名称" tree-node></vxe-table-column>
              <vxe-table-column field="id" title="Id" width="80"></vxe-table-column>
              <vxe-table-column field="type" title="类型" width="80">
                <template slot-scope="scope">{{scope.row.type==1?'导航':'分类'}}</template>
              </vxe-table-column>
              <vxe-table-column field="path" title="访问路径"></vxe-table-column>
              <vxe-table-column field="listPage" title="列表页"></vxe-table-column>
              <vxe-table-column field="detailPage" title="详情页"></vxe-table-column>
              <vxe-table-column title="状态">
                <template slot-scope="scope">
                  <el-switch
                    :active-value="1"
                    :inactive-value="0"
                    v-model="scope.row.status"
                    @change="statusChange(scope.row)"
                  ></el-switch>
                </template>
              </vxe-table-column>
              <vxe-table-column field="sort" title="顺序" width="80" :edit-render="{name: 'input'}"></vxe-table-column>
              <vxe-table-column label="操作" width="120">
                <template slot-scope="scope">
                  <el-button type="text" @click="edit(scope.row)">编辑</el-button>
                  <el-button type="text" @click="del(scope.row)">删除</el-button>
                </template>
              </vxe-table-column>
            </vxe-table>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="新增扩展字段" width="500px" :visible.sync="showExtForm">
      <el-form :model="extParams" label-position="right" label-width="80px">
        <el-form-item label="名称">
          <el-input v-model="extParams.name" placeholder="名称：建议2-4字以内"></el-input>
        </el-form-item>
        <el-form-item label="字段">
          <el-input v-model="extParams.field" placeholder="以字母开头，下划线分隔，例：price"></el-input>
        </el-form-item>
        <el-form-item label="类型">
          <el-select v-model="extParams.type" placeholder="请选类型">
            <el-option label="单行文本" value="input"></el-option>
            <el-option label="多行文本" value="textarea"></el-option>
            <el-option label="下拉列表" value="select"></el-option>
            <el-option label="日期选择" value="date"></el-option>
            <el-option label="单选按钮" value="radio"></el-option>
            <el-option label="多选按钮" value="checkbox"></el-option>
            <el-option label="图片上传" value="image"></el-option>
            <el-option label="附件上传" value="file"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="长度">
          <el-input v-model="extParams.size" :disabled="extParams.id != null" placeholder="字段长度"></el-input>
        </el-form-item>
        <el-form-item label="字段值">
          <el-input
            v-model="extParams.value"
            type="textarea"
            placeholder="只在单选、多选、下拉选择时有效，以逗号（，）分隔。 例：红，绿"
          ></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="extParams.description" type="textarea" placeholder="字段描述"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showExtForm = false">取消</el-button>
        <el-button type="primary" @click="confirmField">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import TreeSelect from "@/components/tree-select";
import categoryApi from "@/api/categoryApi";
import templateApi from "@/api/templateApi";
import extFieldApi from "@/api/extFieldApi";
export default {
  components: {
    TreeSelect
  },
  data() {
    return {
      categories: [],
      treeData: [],
      templates: [],
      config: {
        value: "id",
        label: "name",
        children: "children"
      },
      editParams: {
        status: 1,
        type: 1,
        sort: 1,
        showChildren: 1
      },
      parentCategory: { id: 0, name: "顶级栏目" },
      fileList: [], // 图标
      imgList: [], // 大图
      multFileList: [], // 多图
      showExtForm: false,
      extParams: {
        size: 255,
        type: "input",
        struct: "category" // 这里默认为栏目扩展字段
      },
      extFields: [],
      categoryExtFields: {},
      extField: ""
    };
  },
  created() {
    this.getCategorys();
    this.getTemplates();
    this.getExtFields();
  },
  computed: {
    editTypeChange() {
      return this.editType;
    }
  },
  watch: {},
  methods: {
    getCategorys() {
      categoryApi.tree().then(res => {
        this.categories = res.data;
        this.treeData = [{ id: 0, name: "顶级栏目" }, ...res.data];
      });
    },
    getTemplates() {
      templateApi.fileList().then(res => {
        this.templates = res.data;
      });
    },
    nodeClick(node) {
      this.editParams.parentId = node.id;
    },
    save(formName) {
      let that = this;
      if (!this.editParams.name) {
        this.$message.warning("请填写名称");
        return false;
      }
      if (!this.parentCategory.id && this.parentCategory.id != 0) {
        this.$message.warning("请选择父栏目");
        return false;
      }
      if (!this.editParams.listPage) {
        this.$message.warning("请选择列表页");
        return false;
      }
      if (!this.editParams.detailPage) {
        this.$message.warning("请选择详情页");
        return false;
      }

      let tempExtFields = Object.assign({}, this.categoryExtFields); // 防止element ui报错，看着难受
      for (let k in tempExtFields) {
        let obj = tempExtFields[k];
        // 判断是不是checkbox,image,file中的一种
        if (typeof obj == "object") {
          // 判断是不是image或file
          if (obj.join("||").includes("object")) {
            let value = obj.map(o => o.url).join("||");
            tempExtFields[k] = value;
          } else {
            tempExtFields[k] = obj.join("||");
          }
        }
      }

      this.editParams.extFieldList = this.extFields;
      this.editParams.extFields = tempExtFields;
      this.editParams.parentId = this.parentCategory.id;
      this.editParams.icon = this.fileList.map(f => f.url).join("");
      this.editParams.img = this.imgList.map(f => f.url).join("");
      this.editParams.imgs = this.multFileList.map(f => f.url).join("||");
      let id = that.editParams.id;
      if (id) {
        categoryApi.update(that.editParams).then(res => {
          that.reset();
          that.$message.success("保存成功");
        });
      } else {
        categoryApi.create(that.editParams).then(res => {
          that.reset();
          that.$message.success("保存成功");
          that.getCategorys();
        });
      }
    },
    edit(row) {
      this.editParams = row;
      // 图标
      if (row.icon) {
        let img = {};
        img.name = row.icon.split("@@")[1];
        img.url = row.icon;
        this.fileList = [img];
      }

      // 大图
      if (row.img) {
        let img = {};
        img.name = row.img.split("@@")[1];
        img.url = row.img;
        this.fileList = [img];
      }

      // 多图
      if (row.imgs) {
        row.imgs.split("||").map(m => {
          let img1 = {};
          img1.name = m.split("@@")[1];
          img1.url = m;
          this.multFileList.push(img1);
        });
      }
      let data = this.$refs.categoryTree.getRowById(row.parentId);
      let parentName = "顶级栏目";
      if (data) {
        parentName = data.name;
      }
      this.parentCategory = { id: row.parentId, name: parentName };
      this.getExtInfo(row.id);
    },
    getExtInfo(categoryId) {
      this.categoryExtFields = {};
      categoryApi.get(categoryId).then(res => {
        this.categoryExtFields = res.data.extFields || {};
        if (!this.categoryExtFields) {
          return;
        }
        this.extFields.forEach(o => {
          if (o.type == "checkbox") {
            if (this.categoryExtFields[o.field]) {
              this.$set(
                this.categoryExtFields,
                o.field,
                this.categoryExtFields[o.field].split("||")
              );
            }
          } else if (o.type == "image" || o.type == "file") {
            let val = this.categoryExtFields[o.field];
            let list = [];
            if (val) {
              let arr = val.split("||");
              arr.map(e => {
                let m = {};
                m.name = e.split("@@")[1];
                m.url = e;
                m.field = o.field;
                list.push(m);
              });
              this.$set(this.categoryExtFields, o.field, list);
            }
          } else {
            let val = this.categoryExtFields[o.field];
            this.$set(this.categoryExtFields, o.field, val);
          }
        });
      });
    },
    getExtFields() {
      extFieldApi.findAll({ struct: "category" }).then(res => {
        this.extFields = res.data;
      });
    },
    del(row) {
      if (row.children && row.children.length > 0) {
        this.$message.warning("该栏目下有子栏目，不允许删除");
        return;
      }
      categoryApi.delete(row.id).then(res => {
        this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$message.success("已删除");
            this.getCategorys();
            this.reset();
          })
          .catch(() => {});
      });
    },
    statusChange(row) {
      categoryApi.update({ id: row.id, status: row.status }).then(res => {
        this.$message.success("修改成功");
      });
    },
    reset(formName) {
      this.editParams = { status: 1, type: 1, sort: 1, showChildren: 1 };
      this.multFileList = [];
      this.fileList = [];
      this.categoryExtFields = {};
    },
    onSuccess(res, file, fileList) {
      file.url = res.data.url;
      this.fileList = fileList;
    },
    onImgSuccess(res, file, fileList) {
      file.url = res.data.url;
      this.imgList = fileList;
    },
    onMultSuccess(res, file, fileList) {
      file.url = res.data.url;
      this.multFileList = fileList;
    },
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
    handleImgRemove(file, fileList) {
      this.imgList = fileList;
    },
    handleMultRemove(file, fileList) {
      this.multFileList = fileList;
    },
    onExtImgSuccess(res, file, fileList) {
      file.url = res.data.url;
      let extField = res.data.extField;
      this.extField = extField;
      this.extImgList = fileList;
      this.categoryExtFields[extField] = fileList;
    },
    onExtFileSuccess(res, file, fileList) {
      file.url = res.data.url;
      let extField = res.data.extField;
      this.extFileList = fileList;
      this.categoryExtFields[extField] = fileList;
    },
    handleExtImgRemove(file, fileList) {
      this.extField = file.field;
      this.extImgList = fileList;
      this.categoryExtFields[this.extField] = fileList;
    },
    handleExtFileRemove(file, fileList) {
      this.extField = file.field;
      this.extFileList = fileList;
      this.categoryExtFields[this.extField] = fileList;
    },
    confirmField() {
      this.extFields.push(Object.assign({}, this.extParams));
      this.extFields.forEach(o => {
        if (o.type == "checkbox") {
          this.$set(this.categoryExtFields, o.field, []);
        }
      });
      this.extParams = {
        size: 255,
        type: "input",
        struct: "category"
      };
      this.showExtForm = false;
    },
    delExt(ext) {
      console.log(ext);
      if (ext.id) {
        this.$confirm("删除此字段将删除与此字段关联的数据, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          extFieldApi.delete(ext.id).then(res => {
            this.$message.success("删除成功");
            var index = this.extFields.indexOf(ext);
            if (index !== -1) {
              this.extFields.splice(index, 1);
            }
          });
        });
      } else {
        var index = this.extFields.indexOf(ext);
        if (index !== -1) {
          this.extFields.splice(index, 1);
        }
      }
    }
  }
};
</script>

<style>
</style>